@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300;400&display=swap');

.nav {
  margin-top: 120px;
  grid-area: n;
  font-family: 'Inconsolata', monospace;

  .nav_list {
    padding: 0;
    margin: 0;
    width: 170px;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    flex-wrap: wrap;
    justify-content: space-between;

    .nav_item {
      background-color: white;
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 7px;
      width: 80px;
      height: 80px;

      .nav_item_link {
        font-size: 20px;
        color: black;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;

        img {
          width: 30px;
          height: 30px; }
        span {
          font-size: 17px; } } }


    .nav_item:last-child {
      margin-bottom: 0; } } }

